$color: white;
$cardColor: rgb(62, 167, 184);

a {
    appearance: none;
}

.movie-view-card {
    margin-top: 50px;
}

.movie-poster,
.poster-img {
    //background-color: $cardColor;
    background-image: radial-gradient(100% 100% at 100% 0, #cacfd0 0, $cardColor 100%);
    border: 5px black;
    box-shadow: rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, rgba(58, 65, 111, .5) 0 -3px 0 inset;

}

.back-button,
.genre-button,
.director-button,
.fav-button {
    margin-right: 10px;
    margin-top: 10px;
    appearance: none;
    background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #202c86 100%);
    border: 0;
    border-radius: 6px;
    box-shadow: rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, rgba(58, 65, 111, .5) 0 -3px 0 inset;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    //font-family: "JetBrains Mono", monospace;
    height: 48px;
    justify-content: center;
    line-height: 1;
    list-style: none;
    overflow: hidden;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    text-align: left;
    text-decoration: none;
    transition: box-shadow .15s, transform .15s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    will-change: box-shadow, transform;
    font-size: 18px;
}

.back-button:focus,
.director-button:focus,
.fav-button:focus,
.genre-button:focus {
    box-shadow: #3c4fe0 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.back-button:hover,
.director-button:hover,
.fav-button:hover,
.genre-button:hover {
    box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
    transform: translateY(-2px);
}

.description-text {
    font-weight: 500;
}

.genre-button,
.director-button,
.fav-button {
    min-width: 100%;
}